.post {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
  gap: 1.5rem;
}

.post:not(:last-child) {
  padding-bottom: 1.5rem;
  border-bottom: 1px solid rgb(200, 200, 200);
}

.post img {
  width: 6rem;
  height: 6rem;
  object-fit: cover;
  border-radius: 100%;
}
