.userInfo {
  display: grid;
  grid-template-columns: min-content 1fr;
  margin-bottom: 3rem;
}

.userInfo img {
  width: 20rem;
  height: 20rem;
  object-fit: cover;
  border-radius: 100%;
  margin-right: 3rem;
}

.status {
  margin-bottom: 3rem;
}

.fullName {
  margin-top: 0;
}
